import React from "react"

import LoginForm from "./forms/LoginForm"
import RegistrationForm from "./forms/RegistrationForm"

const LoginAndRegistration = () => {
	return (
		<div id="login-page">
			<LoginForm/>
			<RegistrationForm/>
		</div>
	)
}

export default LoginAndRegistration






